import { Container, Row, Col } from "react-bootstrap";
import meImg from "../assets/img/GaetanRossignol.jpg";

export const Presentation = () => {

    return (
        <section className="presentation" id="presentation">
            <Container>
                <Row className="presentationRow">
                <h2>Qui suis-je ?</h2>
                <Col xs={12} xl={6} className="text-center">
                    <div>
                        <p>Bonjour, je suis Gatan Rossignol, dveloppeur web junior et crateur de sites internet.</p>
                        <p>Autodidacte et reconverti dans le numrique en 2021, jai commenc  mauto-former sur les langages HTML, CSS et JS en formation LinkedIn et youtube.</p>
                        <p>En 2021, jai intgr la formation dveloppeur web (Openclassrooms) dans lequel jai pu consolider ces acquis et travailler sur dautres domaines (Back-End tel que Node.JS et Express.JS, utiliser des BDD commme MongoDB et MySQL, le SEO ou encore en web design).</p>
                        <p>Passionn par les nouvelles technologies, Je suis  la recherche active d'une entreprise innovante. </p>
                    </div>
                </Col>
                <Col xs={12} md={6} xl={5}>
                    <img className="presentationImg" src={meImg} alt="Presentation Img"/>
                </Col>
                </Row>
            </Container>
            </section>
    );
}