import { useState, useEffect } from "react";
import { Container, Navbar, Nav } from "react-bootstrap";
import { useLocation } from "react-router-dom";
import logo from "../assets/img/Logo.jpg";
import github from "../assets/img/github.svg";
import linkedin from "../assets/img/linkedin.svg";

export const NavBar = () => {
  // Ce code importe le hook useLocation et dfinit des variables dtat pour le lien actif et indique si lutilisateur a fait dfiler.
  const location = useLocation();
  const [activeLink, setActiveLink] = useState("Home");
  const [scrolled, seScrolled] = useState(false);

  // Ce hook useEffect ajoute un couteur dvnement  lobjet window pour suivre le comportement de dfilement.
  useEffect(() => {
    const onScroll = () => {
      if (window.scrollY > 50) {
        seScrolled(true);
      } else {
        seScrolled(false);
      }
    };
    window.addEventListener("scroll", onScroll);
    return () => window.removeEventListener("scroll", onScroll);
  });

  // Cette fonction met  jour la variable dtat de lien actif.
  const onUpdateActiveLink = (value) => {
    setActiveLink(value);
  };

  // Dtermine si la page active est la page daccueil en vrifiant le chemin daccs actuel par rapport  plusieurs pages de dtails du projet. Le rsultat de cette vrification est stock dans la constante isHomePage, ce qui sera vrai si la page active est lune des pages de dtail du projet et false dans le cas contraire.
  const isHomePage =
    location.pathname === "/portfolio-personnel/ProjectDetail/Reservia" ||
    location.pathname === "/portfolio-personnel/ProjectDetail/Ohmyfood" ||
    location.pathname === "/portfolio-personnel/ProjectDetail/Orinoco" ||
    location.pathname === "/portfolio-personnel/ProjectDetail/Piiquante" ||
    location.pathname === "/portfolio-personnel/ProjectDetail/LaChouetteAgence" ||
    location.pathname === "/portfolio-personnel/ProjectDetail/Groupomania";

  return (
    <Navbar expand="md" className={scrolled ? "scrolled" : ""}>
      <Container>
        {/* Le composant Navbar.Brand contient un lien vers la page d'accueil et affiche le logo du site Web. */}
        <Navbar.Brand href="/portfolio-personnel/">
          <img id="logo" src={logo} alt="Logo" />
        </Navbar.Brand>
        {/* Le composant Navbar.Toggle cre un bouton qui bascule l'affichage des liens de la barre de navigation sur les petits crans. */}
        <Navbar.Toggle aria-controls="basic-navbar-nav">
          <span className="navbar-toggler-icon"></span>
        </Navbar.Toggle>
        {/* Le composant Navbar.Collapse contient les liens qui seront affichs lorsque la barre de navigation sera dveloppe. */}
        <Navbar.Collapse>
          <Nav id="basic-navbar-nav" className="navbar-nav mr-auto">
            {/* les composants Nav.Link crent des liens vers la page d'accueil et d'autres sections du site Web. Le nom de classe est dfini sur "lien de barre de navigation actif" lorsque le lien est actuellement slectionn. */}
            <Nav.Link
              href="/portfolio-personnel/#home"
              className={
                activeLink === "home" ? "active navbar-link" : "navbar-link"
              }
              onClick={() => onUpdateActiveLink("home")}
            >
              Accueil
            </Nav.Link>
            {/* Les deux composants Nav.Link suivants ne seront affichs que si la page actuelle n'est pas la page d'accueil. */}
            {!isHomePage && (
              <Nav.Link
                href="/portfolio-personnel/#skills"
                className={
                  activeLink === "skills" ? "active navbar-link" : "navbar-link"
                }
                onClick={() => onUpdateActiveLink("skills")}
              >
                Comptences
              </Nav.Link>
            )}
            {!isHomePage && (
              <Nav.Link
                href="/portfolio-personnel/#projects"
                className={
                  activeLink === "projects"
                    ? "active navbar-link"
                    : "navbar-link"
                }
                onClick={() => onUpdateActiveLink("projects")}
              >
                Projets
              </Nav.Link>
            )}
            {!isHomePage && (
              <Nav.Link
                href="/portfolio-personnel/#presentation"
                className={
                  activeLink === "presentation"
                    ? "active navbar-link"
                    : "navbar-link"
                }
                onClick={() => onUpdateActiveLink("presentation")}
              >
                Presentation
              </Nav.Link>
            )}
            {!isHomePage && (
              <Nav.Link
                href="/portfolio-personnel/#contact"
                className={
                  activeLink === "contact"
                    ? "active navbar-link"
                    : "navbar-link"
                }
                onClick={() => onUpdateActiveLink("contact")}
              >
                Contact
              </Nav.Link>
            )}
            {/* Le code suivant affiche les icnes des mdias sociaux dans la barre de navigation. */}
            <span className="navbar-text">
              <div className="social-icon">
                <a
                  href="https://github.com/RGaetanGranstek"
                  title="Liens vers la page github de gatan rossignol"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <img src={github} alt="logo github" />
                </a>
                <a
                  href="https://www.linkedin.com/in/gaetan-rossignol-60024a174/"
                  title="Liens vers la page linkedin de gatan rossignol"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <img src={linkedin} alt="logo linkedin" />
                </a>
              </div>
            </span>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
};
